@import "char"; //podstawowe stylowanie dla wykresu - musi być dołączony

$colors: #AAD962, #FBBF45, #EF6A32, #ED0345, #A12A5E;

$heights: (
        key1: 300px,
        key2: 230px,
        key3: 380px,
        key4: 290px,
        key5: 130px,
);

$widths: (
  w1: 50px,
  w2: 100px,
  w3: 150px,
  w4: 200px,
);




.bar1 {
  background-color: nth($colors, 1);
  height: map_get($heights, key1);
}

.bar2 {
  background-color: nth($colors, 2);
  height: map_get($heights, key2);
}

.bar3 {
  background-color: nth($colors, 3);
  height: map_get($heights, key3);
}

.bar4 {
  background-color: nth($colors, 4);
  height: map_get($heights, key4);
}

.bar5 {
  background-color: nth($colors, 5);
  height: map_get($heights, key5);
}
